var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('article',{staticClass:"woodwork"},[_c('section',{staticClass:"woodwork__ch woodwork__ch--1"},[_c('h1',{staticClass:"woodwork__ch-title"},[_vm._v("回鄉 走入傳統木作之路")]),_c('section',{staticClass:"woodwork__ch-content"},[_c('p',[_vm._v("\n        今年剛進入不惑之年的楊詔期先生，從事木工產業已逾16年。曾在土木工程業任職，後來逐漸地體會到土木工程並非自己的志業，因此在2008年毅然決然地回到水林老家。\n      ")]),_c('img',{staticClass:"--size-l --center",attrs:{"src":(_vm.publicPath + "img/detail/woodwork/tools.jpg"),"alt":"tools"}}),_c('p',[_vm._v("\n        從事土木工程期間，他經常看到海岸邊的漂流木，因為從小就對於木頭有著深厚的喜愛和嚮往，他認為木頭是珍貴的天然資源，應該賦予更多的價值和使用，便聽從內心的聲音，開始拜師學藝，接觸傳統的木作「榫接」。這種技法的特殊性是不使用現代五金，憑著木條的卡榫、不同凹槽放置、拼湊出非常堅固的木作結構，展現簡潔又俐落的美感。也因為接觸了傳統「榫接」技術，開啟了他成立工作室的決心。\n      ")])])]),_c('section',{staticClass:"woodwork__ch woodwork__ch--2"},[_c('h1',{staticClass:"woodwork__ch-title"},[_vm._v("木頭 是生活和生命的日常對話")]),_c('section',{staticClass:"woodwork__ch-content"},[_c('p',[_vm._v("\n        當我們走進「五木工坊」的工作室，撲鼻而來的木頭香氣，浸染工作室四周的氛圍，隨意陳設的木工藝品，在古典音樂的音律環繞下，令人感受氣定神閒的樸實與沉靜。「五木工坊」的由來，來自於台灣特有針、闊五木。楊先生說道：「每一種木頭的紋路，都代表著其獨特的DNA，在歷經時間的淬鍊，演繹出成熟與穩重的樣態，宛如智者的生命一般、不同凡響。」。\n      ")]),_c('img',{staticClass:"--size-m --inline-block",attrs:{"src":(_vm.publicPath + "img/detail/woodwork/木椅.jpg"),"alt":"木椅"}}),_c('img',{staticClass:"--size-m --inline-block",attrs:{"src":(_vm.publicPath + "img/detail/woodwork/燈2.jpg"),"alt":"燈"}}),_c('p',[_vm._v("\n        從小就喜歡木頭溫潤的質地，他認為：「木頭像似人與人之間的相處，需要花時間彼此了解，做為生活和生命的一種日常對話。」木頭的用途很多、也很廣，大至建築、小至木筷，可以提煉成精油，也可以將木削當作堆肥柴火，歸還於大自然，與之融為一體。\n      ")])])]),_c('section',{staticClass:"woodwork__ch woodwork__ch--3"},[_c('h1',{staticClass:"woodwork__ch-title"},[_vm._v("木作 傳遞出最純真的喜悅")]),_c('section',{staticClass:"woodwork__ch-content"},[_c('img',{staticClass:"--size-l --center",attrs:{"src":(_vm.publicPath + "img/detail/woodwork/wooden_dummny.jpg"),"alt":"wooden_dummny"}}),_c('p',[_vm._v("\n        木工手作的靈感來自生活中不同接觸和體悟。手作的本質呈現的是一種單純且令人感動的痕跡，同時靜靜地勾勒出簡約的線條，體現木頭樸質的紋路以及沉穩的呼吸與溫潤的質性。每一件獨特作品都隱含著「適材適所」的哲思。\n      ")]),_c('p',[_vm._v("\n        楊先生認為，人生是一連串不斷地選擇，最幸福的人是了解自己的本性，傾聽內在的聲音，而生活的「適情適性」。在實現夢想的旅程中，自己幸運的能夠將工作與興趣結合並融入生活，讓工作不再只是一份工作。因此木作能自然的傳遞出最純真的喜悅且最單純的感動，進而熱愛自己的一切所作。\n      ")])])]),_c('section',{staticClass:"woodwork__ch woodwork__ch--4"},[_c('h1',{staticClass:"woodwork__ch-title"},[_vm._v("傳承 榫接工藝教育")]),_c('section',{staticClass:"woodwork__ch-content"},[_c('p',[_vm._v("\n        早期沒有太多人去學習榫接技術，而現在許多木工愛好者紛紛都想來習藝。他說：「習藝需要深具熱情及興趣，才能克服繁瑣的技術和工序。現在要培育這方面的人才，兼具極大的考驗和試煉。」希望未來有更多的年輕人，可以找到自己的喜愛。\n      ")]),_c('p',[_vm._v("\n        他在鄰近的教育單位開設木工課程，透過教導學員有關於木作的知識和實作，讓木作技能嵌入學校教育，甚至成為一種生活樂趣。手工DIY課程的開展，目的在啟發學員的創造潛力。\n      ")]),_c('img',{staticClass:"--size-l --center",attrs:{"src":(_vm.publicPath + "img/detail/woodwork/window_wood-cut.jpg"),"alt":"window_wood-cut"}})])]),_c('section',{staticClass:"woodwork__ch woodwork__ch--5"},[_c('h1',{staticClass:"woodwork__ch-title"},[_vm._v("初心 堅持找回生活本意")]),_c('section',{staticClass:"woodwork__ch-content"},[_c('p',[_vm._v("\n        木作如同生活的智者，帶領我們不斷地去學習，透過不同形式和表現，描述著他們所經歷的過程，提升我們的想法與見解。像似相對論的人生哲理，穿越古今中外，不同時代背景，不管科技的日新月異，都讓我們了解追求好物，需要以古鑑今，做為創造的本意。\n      ")]),_c('img',{staticClass:"--size-l --center",attrs:{"src":(_vm.publicPath + "img/detail/woodwork/時光刻度.jpg"),"alt":"時光刻度"}})])])])}
